@import url(https://fonts.googleapis.com/css2?family=Baloo+Tamma+2:wght@400;600;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
    line-height: 1.15;
    -webkit-text-size-adjust: 100%;
}

main {
    display: block;
}

h1 {
    font-size: 2em;
    margin: 0.67em 0;
}

hr {
    box-sizing: content-box;
    height: 0;
    overflow: visible;
}

pre {
    font-family: monospace, monospace;
    font-size: 1em;
}

a {
    background-color: transparent;
}

abbr[title] {
    border-bottom: none;
    text-decoration: underline;
    -webkit-text-decoration: underline dotted;
            text-decoration: underline dotted;
}

b,
strong {
    font-weight: bolder;
}

code,
kbd,
samp {
    font-family: monospace, monospace;
    font-size: 1em;
}

small {
    font-size: 80%;
}

sub,
sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline;
}

sub {
    bottom: -0.25em;
}

sup {
    top: -0.5em;
}

img {
    border-style: none;
}

button,
input,
optgroup,
select,
textarea {
    font-family: inherit;
    font-size: 100%;
    line-height: 1.15;
    margin: 0;
}

button,
input {
    overflow: visible;
}

button,
select {
    text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
    -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
    border-style: none;
    padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
    outline: 1px dotted ButtonText;
}

fieldset {
    padding: 0.35em 0.75em 0.625em;
}


legend {
    box-sizing: border-box;
    color: inherit;
    display: table;
    max-width: 100%;
    padding: 0;
    white-space: normal;
}

progress {
    vertical-align: baseline;
}

textarea {
    overflow: auto;
}

[type="checkbox"],
[type="radio"] {
    box-sizing: border-box;
    padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
    height: auto;
}

[type="search"] {
    -webkit-appearance: textfield;
    outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}

::-webkit-file-upload-button {
    -webkit-appearance: button;
    font: inherit;
}

details {
    display: block;
}

summary {
    display: list-item;
}

template {
    display: none;
}

[hidden] {
    display: none;
}
/* font-family */

:root {
	--red-highlight: #f08080;
	--blue-background: #a8dadc;
	--light-blue: #d4eded;
	--dark-blue: #457b9d;
}
body,
html {
	display: flex;
	background-image: url(data:image/png;base64,);
	font-family: "Baloo Tamma 2";
	flex-direction: column;
}

/* nav */
.ham {
	background-color: transparent;
	border: none;
	color: white;
	padding: 30px;
	font-size: 20px;
}
nav {
	grid-area: nav;
	flex: 0 1 auto;
}
.nav-items {
	display: flex;
	justify-content: space-around;
	max-width: 1500px;
	margin: 0 auto;
}
nav a {
	padding: 30px;
	font-weight: 600;
	text-decoration: none;
	color: white;
	font-size: 25px;
}
nav .home {
	margin-right: 40%;
	color: #f08080;
	color: var(--red-highlight);
}


/* footer */
footer {
	flex: 0 1 auto;
    display: block;
	color: white;
	text-align: center;
	background-color: #457b9d;
	background-color: var(--dark-blue);
	padding: 5px;
}
.decor {
	flex: 0 1 auto;
	background-color: #f08080;
	background-color: var(--red-highlight);
	padding: 5px;
}
.copyright {
    display: flex;
    margin: 15px;
    justify-content: center;
}
.copyright .company {
    margin: 0;
}
.copyright .page-top {
    position: absolute;
    right: 50px;
    color: white;
    text-decoration: none;
}
.copyright .page-top:hover {
    color: #f08080;
    color: var(--red-highlight);
}

/* index.html */
/* header */
header {
    flex: 1 0 auto;
	height: 100vh;
	background-color: black;
	
}
header ul {
	color: white;
	display: flex;
	justify-content: space-around;
	margin: 0 10%;
}
header li {
	list-style-type: none;
	font-size: 150px;
	padding: 33vh 2%;
}
/* header .letter li:nth-child(1):hover ~ header{
	background-color: #f08080;
	// implement with js
} */


/* main content */
#about-me {
	display: flex;
	flex-direction: column;
	flex: 1 0 auto;
	background-color: #a8dadc;
	background-color: var(--blue-background);
	margin: 5% auto;
	border-radius: 10px;
	padding: 3%;
	max-width: 1400px;
	justify-self: center;
}
#about-me h1 {
	font-size: 40px;
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
}
#about-me p {
	font-size: 20px;
}
#about-me h1,
#about-me p {
	background-color: #d4eded;
	background-color: var(--light-blue);
	padding: 5px 10px;
	border-radius: 10px;
	margin: 10px;
}
#about-me .main-content {
	display: grid;
	grid-template-areas: "img""main-text";
}
#about-me .main-text {
	display: flex;
	flex-direction: column;
	align-self: center;
	grid-area: main-text;
	max-width: 600px;
	margin: 2%;
}
#about-me .main-content img {
	display: flex;
	align-self: center;
	justify-self: center;
	grid-area: img;
	border-radius: 10px;
	margin: 5% 5% 5% 0;
	max-width: 90%;
	object-fit: cover;
}

/* contact form */
#contact-me {
	background-color: lightgray;
	display: grid;
	grid-template-areas: 
		". heading 	aside ."
		". form 	aside ."
		". form 	aside .";
	grid-template-columns: 20% 50% 10% 20%;
	/* height: 60vh; */
}
#contact-me h1 {
	grid-area: heading;
	background-color: #f08080;
	background-color: var(--red-highlight);
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
	justify-self: center;
	align-self: flex-end;
	margin-top: 10%;
	padding: 5px 10px;
	border-radius: 3px;
}
#contact-me form {
	grid-area: form;
	align-self: flex-start;
	justify-self: center;
	margin-bottom: 10%;
}
#contact-me form > * {
	margin: 2%;
	border: none;
	border-radius: 3px;
}
#contact-me form > label {
	margin-bottom: 0;
}
#contact-me form > input,
#contact-me form > textarea {
	padding: 5px;
}
#contact-me form button {
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
	margin: 0 auto;
	padding: 5px 10px;
}
#contact-me form,
#contact-me aside {
	display: flex;
	flex-direction: column;
	width: 100%;
	max-width: 700px;
}

/* links */
#contact-me aside {
	grid-area: aside;
	align-self: center;
	text-align: center;
	margin-top: 30px;
}
#contact-me aside a {
	color: black;
	text-decoration: none;
	margin: 10%;
	font-size: 25px;
}
#contact-me aside a:hover {
	color: #457b9d;
	color: var(--dark-blue);
}
#contact-me aside p {
	font-family: "Baloo Tamma 2";
	font-weight: 800;
	font-size: 16px;
	text-decoration: underline;
}


/* gallery.html */
.gallery nav a {
	color: #f08080;
	color: var(--red-highlight);
}
.gallery nav .ham {
	color: black;
}
.gallery {
	display: flex;
	flex-direction: column;
}
.gallery-item {
	flex: 1 0 auto;
	width: 100%;
	display: grid;
	grid-template-rows: auto;
	grid-template-columns: 0 1fr 0;
	
	margin-bottom: 2%;
	overflow: hidden;
}
.gallery-item img {
	grid-row: 1 / 2;
	grid-column: 1 / 4;
	width: 100%;
	max-height: 400px;
	object-fit: cover;
	-webkit-filter: blur(5px) brightness(0.45);
	        filter: blur(5px) brightness(0.45);
}
.gallery-item .content {
	grid-row: 1 / 2;
	grid-column: 2 / 3;
	z-index: 2;
	margin: 0 0 3% 3%;
	display: flex;
	flex-direction: column;
	align-self: flex-end;
	color: white;
}
.gallery-item .heading {
	display: flex;
	flex-direction: row;
	align-items: center;
}
.gallery-item .heading a {
	font-size: 20px;
	padding: 1%;
	padding-bottom: 1.5%;
	text-decoration: none;
}
.gallery-item a {
	color: white;
}
.gallery-item .heading a:hover,
.gallery-item a:hover {
	color: #f08080;
	color: var(--red-highlight);
}


@media screen and (min-width: 400px) {
	#about-me .main-content img {
		margin: 5%;
	}
}

@media screen and (min-width: 800px) {
	#about-me .main-content {
		grid-template-areas: "img main-text";
		max-height: 700px;
	}
	
}


.modal-full {
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 80vw;
	max-width: 1200px;
	height: 80vh;
	max-height: 700px;
	z-index: 50;
	padding: 3%;
	overflow: auto;
	color: black;
	background-color: white;
}

.header-links {
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	justify-content: space-between;
}
.header-links div {
	padding: 5px;
}
.header-links .fa-times {
	font-size: 20px;
}
.header-links a:hover {
	color: coral;
}

.modal-content img {
	width: 100%;
}
.modal-content .imagelist {
	list-style-type: none;
}

.modal-bg {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100%;
	z-index: 20;
	background-color: rgba(0, 0, 0, 0.603);
}

